import { Component, OnInit } from '@angular/core';
import { InstaServiceService } from './insta-service.service';

@Component({
  selector: 'app-insta',
  templateUrl: './insta.component.html',
  styleUrls: ['./insta.component.sass']
})
export class InstaComponent implements OnInit {

  username: string = "";
  JsonData: any = [];
  imageUrl: string = "";
  fullname: string = "";
  loading: boolean = false;
  biography: string;
  followers: string;

  constructor(private _InstaServiceService: InstaServiceService) { }

  ngOnInit() {
  }

  getImage() {
    this.loading = true
    this._InstaServiceService.get_image(this.username).subscribe(data => {
      this.JsonData = data;
      this.imageUrl = this.JsonData.graphql.user.profile_pic_url_hd;
      this.fullname = this.JsonData.graphql.user.full_name;
      this.biography = this.JsonData.graphql.user.biography;
      this.followers = this.JsonData.graphql.user.edge_followed_by.count
      this.loading = false
    });
  }

}
